/**
 * This is a fork of a repeater created by Leon Revill (https://github.com/RevillWeb/rebel-repeater).
 *
 * Blog: http://www.revilweb.com
 * GitHub: https://github.com/RevillWeb
 * Twitter: @RevillWeb
 */

class TemplateRepeater extends HTMLElement {

    createdCallback() {
        this.shadow = this.createShadowRoot();
    }

    attachedCallback() {
        this.render();
    }

    render() {
        const renderError = "Content should be an Array of objects.";
        const content = TemplateRepeater.fromJson(this.getAttribute('content'));
        const template = this.shadow.querySelector('template').cloneNode(true);

        this.shadow.innerHTML = Array.isArray(content) ? content.map(mapTemplate).join('') : new Error(renderError);

        function mapTemplate(item){
            return TemplateRepeater.interpolate(template, item);
        }

        return this.shadow.innerHTML;
    }

    attributeChangedCallback(name) {
        switch (name) {
            case "content":
                this.render();
                break;
        }
    }

    static interpolate(template, obj) {
        if (typeof obj == "object") {
            for (var key in obj) {
                const searchPattern = "${" + key + "}";
                if (template.innerHTML.indexOf(searchPattern) > -1) {
                    template = template.innerHTML.replace(searchPattern, obj[key]);
                    delete obj[key];
                }
            }
        }
        return template;
    }

    static fromJson(str) {
        let obj = null;
        if (typeof str == "string") {
            try { obj = JSON.parse(str);
            } catch (e) { throw new Error("Invalid JSON string provided. "); }
        }
        return obj;
    }
}

document.registerElement("template-repeater", TemplateRepeater);


